// About half of the code in this file was borrowed from
// https://www.geeksforgeeks.org/timsort/

import { range, swap } from "./util";

var addToHistory;

export function sort(props) {
    addToHistory = props.addToHistory;
    // Do the sorting
    const sortedArray = timSort(props.array);
    // Finish the history by adding the final sorted array.
    addToHistory({array: sortedArray, highlights: []});
    return sortedArray;
}

const MIN_MERGE = 16;

function timSort(array) {
    let n = array.length;
    // Find the minimum run optimal for merging
    const minRun = calculateMinRun(n);

    // Use insertion sort to sort each sequential run in the array.
    for (let start = 0; start < n; start += minRun) {
        const end = Math.min(start + minRun - 1, n - 1);
        insertionSort(array, start, end);
        // Show the section that has just been sorted
        addToHistory({array: array, highlights: range(start, end+1)});
    }

    let size = minRun;
    while (size < n) {
        for (let start = 0; start < n; start += 2 * size) {
            // Split the current sort range
            const split = Math.min(n - 1, start + size - 1);
            const end = Math.min((start + 2 * size - 1), (n - 1));
            if (split < end) {
                // Merge the left and right arrays if elements are out of order. Otherwise,
                // they're already sorted.
                if (array[split] >= array[split+1]) {
                    merge(array, start, split, end);
                }
                // Show the section that has just been sorted
                addToHistory({array: array, highlights: range(start, end+1)});
            }
        }
        size *= 2;
    }
    return array;
}

// Find the minimum run length that's optimal for merging
function calculateMinRun(n) {
    let r = 0;
    while (n >= MIN_MERGE) {
        r |= (n & 1);
        n >>= 1;
    }
    return n + r;
}

// Standard insertion sort
function insertionSort(array, start, end) {
    if (end <= start) return array;
    // Sort the section using insertion method
    for (let i = start+1; i < end+1; i++) {
        let j = i;
        addToHistory({array: array, highlights: [j-1, j]});
        while (j > start && array[j] < array[j-1]) {
            swap(array, j-1, j);
            addToHistory({array: array, highlights: [j-1, j]});
            j--;
        }
    }
    return array;
}

// Standard merge function
function merge(array, start, split, end) {
    if (end <= start) return array;

    let mergeArray = [];
    let i = start, j = split+1;
    while (i <= split && j <= end) {
        addToHistory({array: array, highlights: [i, j]});
        if (array[i] <= array[j]) {
            mergeArray.push(array[i++]);
        } else {
            mergeArray.push(array[j++]);
        }
    }

    while (i <= split) {
        addToHistory({array: array, highlights: [i, j-1]});
        mergeArray.push(array[i++]);
    }
    while (j <= end) {
        addToHistory({array: array, highlights: [i-1, j]});
        mergeArray.push(array[j++]);
    }

    for (let k = 0; k < mergeArray.length; k++) {
        addToHistory({array: array, highlights: [start+k]});
        array[start+k] = mergeArray[k];
    }

    return array;
}
